import uniLoadMore from '@/components/uni-load-more/uni-load-more.vue';
var page = 1;
import {
	mapState
} from 'vuex';
export default {
	components: {
		uniLoadMore
	},
	data() {
		return {
			cateMaskState: 0, //分类面板展开状态
			headerPosition: "fixed",
			headerTop: "0px",
			loadingType: 'more', //加载更多状态
			filterIndex: 0,
			cateId: 0, //已选三级分类id
			priceOrder: 0, //1 价格从低到高 2价格从高到低
			cateList: [],
			goodsList: [],
			hotelList: [],
			sortType: 'LowRate',
			compareFlag: false,
			// news:[]
		};
	},
	computed: {
		...mapState(['userInfo'])
	},
	onLoad(options) {

		// console.log(pricepencent)
		// localStorage.clear();



		this.cateId = options.tid;
		this.loadCateList(options.fid, options.sid);
		this.loadData();
		// this.getData();
	},
	onPageScroll(e) {
		//兼容iOS端下拉时顶部漂移
		if (e.scrollTop >= 0) {
			this.headerPosition = "fixed";
		} else {
			this.headerPosition = "absolute";
		}
	},
	//下拉刷新
	onPullDownRefresh() {
		// this.loadData('refresh');
	},
	//加载更多
	onReachBottom() {
		this.loadMoreData();
	},
	methods: {

		//加载分类
		async loadCateList(fid, sid) {
			let list = await this.$api.json('cateList');
			let cateList = list.filter(item => item.pid == fid);

			cateList.forEach(item => {
				let tempList = list.filter(val => val.pid == item.id);
				item.child = tempList;
			})
			this.cateList = cateList;
		},
		//加载商品 ，带下拉刷新和上滑加载
		async loadData(type = 'add', loading) {
			this.nowTime = new Date().getTime().toString();
			this.timestamp = this.nowTime.substring(0, 10);
			var localdata = JSON.parse(localStorage.getItem('params'))
			// console.log(localdata);

			//参数1：timestamp  ---当前时间搓
			var timestamp = this.timestamp;
			//参数2：signature  --- md5(timestamp + md5(data + appkey) + secretKey)  
			// var signature = this.$md5(timestamp + this.$md5(data + appkey) + secretKey);
			//参数3：data
			var data = {
				"Version": "1.36",
				"Local": "zh_CN",
				"Request": {
					"ArrivalDate": localdata.startTime,
					"DepartureDate": localdata.endTime,
					"CityId": "1401",
					"QueryText": localdata.hotelName,
					"PaymentType": "All",
					"Sort": "Default",
					"PageIndex": page,
					"PageSize": "10",
					"ResultType": "1,2,3,4"
				}
			}
			// signature =  md5(timestamp + md5(data + appkey) + secretKey) 
			var params = JSON.stringify(data)
			var data2 = encodeURIComponent(params)
			var signature = this.$md5(timestamp + this.$md5(params + localdata.appkey) + localdata.secretKey);

			uni.request({
				url: this.api + '/api/el',
				data: {
					"elUrl": "http://api.elong.com/rest?format=json&method=hotel.list&user=ea6caf12111045358e50018742b4c47a&timestamp=" +
						timestamp + "&signature=" + signature + "&data=" + data2,
				},
				success: (res) => {
					var arr = []
					arr = res.data.data
					var response = decodeURIComponent(arr)
					if (JSON.parse(response) != null) {
						var listdata=[]
						listdata = JSON.parse(response).Result.Hotels;
						this.list = JSON.parse(response).Result.Hotels
						console.log(this.list)
						listdata.map((val)=>{
							// console.log(val)
							val.LowRate = Math.round(val.LowRate * this.userInfo.pricePencent)
						})
						this.goodsList = listdata
					} else {
						console.log('数据不存在')
					}
				}
			});
		},
		async loadMoreData(type = 'add', loading) {
			var goodlist = this.goodsList;
			this.nowTime = new Date().getTime().toString();
			this.timestamp = this.nowTime.substring(0, 10);
			var localdata = JSON.parse(localStorage.getItem('params'));
			page++;
			// localStorage.setItem('key', JSON.stringify(this.params));
			//参数1：timestamp  ---当前时间搓
			var timestamp = this.timestamp;
			//参数2：signature  --- md5(timestamp + md5(data + appkey) + secretKey)  
			// var signature = this.$md5(timestamp + this.$md5(data + appkey) + secretKey);
			//参数3：data
			var data = {
				"Version": "1.36",
				"Local": "zh_CN",
				"Request": {
					"ArrivalDate": localdata.startTime,
					"DepartureDate": localdata.endTime,
					"CityId": "1401",
					"QueryText": localdata.hotelName,
					"PaymentType": "All",
					"Sort": "Default",
					"PageIndex": page,
					"PageSize": "10",
					"ResultType": "1,2,3,4"
				}
			}
			// signature =  md5(timestamp + md5(data + appkey) + secretKey) 
			var params = JSON.stringify(data)
			var data2 = encodeURIComponent(params)
			var signature = this.$md5(timestamp + this.$md5(params + localdata.appkey) + localdata.secretKey);

			uni.request({
				url: this.api + '/api/el',
				data: {
					"elUrl": "http://api.elong.com/rest?format=json&method=hotel.list&user=ea6caf12111045358e50018742b4c47a&timestamp=" +
						timestamp + "&signature=" + signature + "&data=" + data2,
				},
				success: (res) => {
					var arr = []
					arr = res.data.data
					var response = decodeURIComponent(arr)
					// this.resdata = JSON.parse(response).Result.Hotels;
					// this.list = JSON.parse(response).Result.Hotels
					// this.resdata.map((val)=>{
					// 	val.LowRate = Math.round(val.LowRate * this.userInfo.pricePencent)
					// })
					// 
					var listdata=[]
					listdata = JSON.parse(response).Result.Hotels;
					this.list = JSON.parse(response).Result.Hotels
					console.log(page,this.list)
					listdata.map((val)=>{
						// console.log(val)
						val.LowRate = Math.round(val.LowRate * this.userInfo.pricePencent)
					})
					this.goodsList = this.goodsList.concat(listdata);
				}
			});
		},
		//筛选点击
		sort(type) {
			this.sortType = type;
			this.compareFlag = !this.compareFlag
			this.goodsList.sort(this.compare(type));
			switch (type) {
				case 'in_price':
					this.sortType = 'in_price';
					this.recommendlist.sort(this.compare('in_price'));
					break;
				case 'now_price':
					this.sortType = 'now_price';
					this.recommendlist.sort(this.compare('now_price'));
					break;
				case 'increase':
					this.sortType = 'increase';
					this.recommendlist.sort(this.compare('increase'));
					break;
			}
		},
		compare(attr) {
			if (this.compareFlag) {
				return function(a, b) {
					var val1 = a[attr];
					var val2 = b[attr];
					return val2 - val1;
				}
			} else {
				return function(a, b) {
					var val1 = a[attr];
					var val2 = b[attr];
					return val1 - val2;
				}
			}
		},

		//显示分类面板
		toggleCateMask(type) {
			let timer = type === 'show' ? 10 : 300;
			let state = type === 'show' ? 1 : 0;
			this.cateMaskState = 2;
			setTimeout(() => {
				this.cateMaskState = state;
			}, timer)
		},
		//分类点击
		changeCate(item) {
			this.cateId = item.id;
			this.toggleCateMask();
			uni.pageScrollTo({
				duration: 300,
				scrollTop: 0
			})
			// this.loadData('refresh', 1);
			// uni.showLoading({
			// 	title: '正在加载'
			// })
		},
		//详情
		navToDetailPage(item) {
			//测试数据没有写id，用title代替
			let id = item.HotelId;
			uni.navigateTo({
				url: `/pages/product/product?id=${id}`
			})
		},
		stopPrevent() {}
	},
}
